 .shift-list-title  {
    text-align: left;
    font-family: 16px;
    font-weight: bold;
    letter-spacing: 0px;
    color: #000000;
    opacity: 0.6;
  }
 .shift-list {
    display:flex;
    align-items: center;
    /* text-decoration: underline; */
    letter-spacing: 0px;
    color: #306297;
    opacity: 1;
    background: #3a7bd529 0% 0% no-repeat padding-box;
    min-height:40px;
    padding: 0px 12px;
    border-radius: 4px;
    border: 1px solid #3a7bd5;
  }

 .shift-remove {
    padding-left:30px;
  }
  
.shiftCard {
    display:inline-flex;
    /* padding-left:12px; */
    margin-right: 0.75rem;
  }